.header .nav__button span {
  width: 24px;
  height: 2px;
  background: #356ead;
  margin-top: -1px;
  margin-left: -12px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.header .nav__button span:before {
  content: "";
  width: 24px;
  height: 2px;
  background: #356ead;
  margin-top: -1px;
  margin-left: -12px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-5px);
}

.header .nav__button span:after {
  content: "";
  width: 24px;
  height: 2px;
  background: #356ead;
  margin-top: -1px;
  margin-left: -12px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(5px);
}

.header:hover .logo__ball {
  animation-name: ball;
  animation-duration: 1.5s;
  animation-timing-function: linear;
  animation-iteration-count: 1;
}

@keyframes ball {
  10% {
    background: linear-gradient(90deg, #316099 89%, #94bbe9 100%);
    transform: translateX(60%);
  }

  20% {
    background: linear-gradient(90deg, #316099 71%, #94bbe9 100%);
    transform: translateX(120%);
  }

  30% {
    background: linear-gradient(90deg, #316099 33%, #94bbe9 100%);
    transform: translateX(180%);
  }

  40% {
    background: linear-gradient(90deg, #316099 0%, #94bbe9 100%);
    transform: translateX(120%);
  }

  50% {
    z-index: 2;
    background: linear-gradient(90deg, #94bbe9 0% 26%, #316099 99%);
  }

  60% {
    z-index: 2;
    background: linear-gradient(90deg, #94bbe9 0%, #316099 87%);
    transform: translateX(0%);
  }

  70% {
    z-index: 2;
    background: linear-gradient(90deg, #94bbe9 0%, #316099 64%);
  }

  80% {
    z-index: 2;
    background: linear-gradient(90deg, #94bbe9 0%, #316099 34%);
    transform: translateX(-90%);
  }

  90% {
    transform: translateX(-60%);
  }
}

